.facet-wrapper {
  display: block;
  background: #fff;
  border-radius: 5px;
  margin: 10px 60px 10px 10px;

}


.facet {
  border-bottom: 1px solid #eaeaea;
  transition: background .2s;

  &:hover {
    background-color: #f7f7f7;
  }

  &:last-child {
    border-bottom: none;
  }

  &__btn-find {
    position: absolute;
    right: 270px;
    z-index: 9;
    //display: none;
    padding: 7px;
    text-align: center;
    font: 400 14px Roboto,sans-serif;
    color: #fff;
    background: #595959;
    border-radius: 5px 0 0 5px;

    &:after {
      position: absolute;
      top: 50%;
      left: 100%;
      width: 0;
      height: 0;
      margin-top: -6px;
      content: '';
      border: solid transparent;
      border-width: 6px;
      border-color: rgba(89, 89, 89, 0);
      border-left-color: #595959;
      pointer-events: none;
    }

  }

  &__controls {
    padding: 10px;
  }

  &__btn-show {
    display: block;
    padding: 5px 10px;
    text-align: center;
    font: 700 14px Roboto,sans-serif;
    color: #000;
    background: #fff;
    border-radius: 5px;
    border: 1px solid #d8d8d8;
    text-decoration: none;
    transition: color .2s, background .1s;

    &:active {
      background-color: #ededed;
    }

  }

  &__header {
    position: relative;
    padding: 10px 10px 10px 30px;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    transition: color .2s;

    //&:active {
    //  background-color: #f1f1f1;
    //}

    &:after {
      position: absolute;
      top: 50%;
      left: 10px;
      width: 10px;
      height: 10px;
      margin-top: -5px;
      content: "";
      text-align: center;
      background-image: url('../images/drop.svg');
      background-repeat: no-repeat;
      background-position: 50% 50%;
    }

    &_closed {

      &:after {
        transform: rotate(180deg);
      }

    }
  }

  &__title {
    position: relative;
    padding: 0;
    margin: 0;
    font: 700 14px Roboto,sans-serif;
    color: #000;
    transition: color .2s;

    &:hover {
      color: #0D47A1;
    }

  }

  &__body {
    padding: 5px 10px;

    &_closed {
      display: none;
    }

  }

  &-link {
    display: block;
    text-decoration: none;
    margin-bottom: 10px;
    font: 400 14px Roboto,sans-serif;
    color: #0D47A1;
    transition: color .2s;

    &:hover {
      text-decoration: underline;
    }
  }

  &-checkbox {
    position: relative;
    display: block;
    color: #000;
    font: 400 14px Roboto,sans-serif;
    margin: 3px 0;

    & label {
      display: block;
      max-width: 100%;
      cursor: pointer;
      padding-left: 20px;
      transition: color .2s;

      &:hover {
        color: #0D47A1;
      }

    }

    & input[type=checkbox] {
      position: absolute;
      top: 3px;
      margin-left: -20px;
    }
  }

  &-radio {
    position: relative;
    display: block;
    font: 400 14px Roboto,sans-serif;
    color: #000;
    margin: 3px 0;

    & label {
      display: block;
      max-width: 100%;
      cursor: pointer;
      padding-left: 20px;
      transition: color .2s;

      &:hover {
        color: #0D47A1;
      }

    }

    & input[type=radio] {
      position: absolute;
      top: 3px;
      margin-left: -20px;
    }
  }

  &-range {
    position: relative;
    display: block;
    font: 400 14px Roboto,sans-serif;
    color: #000;

    & label {
      display: block;
      max-width: 100%;
      cursor: pointer;
      transition: color .2s;

      &:hover {
        color: #0D47A1;
      }

    }

    &_line {

      & input {
        display: inline-block;
        position: relative;
        width: 100%;
        background-color: #fff;
        border: 1px #d7d7d7 solid;
        padding: 5px;
        border-radius: 3px;
        outline: none;
        margin-bottom: 10px;
        transition: border-color .2s;

        &:focus {
          border-color: #0D47A1;
        }
      }

    }

    &_group {

      & input {
        display: inline-block;
        position: relative;
        width: 49%;
        background-color: #fff;
        border: 1px #d7d7d7 solid;
        padding: 5px;
        border-radius: 3px;
        outline: none;
        margin-bottom: 10px;
        transition: border-color .2s;

        &:focus {
          border-color: #0D47A1;
        }

        &:last-child {
          float: right;
        }

      }

    }
  }

  &__btn {
    position: relative;
    text-align: center;
    text-decoration: none;
    display: block;
    width: 100%;
    font-size: 0.875em;
    font-weight: 300;
    background-color: #fff;
    color: #444;
    border: 1px #b1b1b1 solid;
    border-radius: 3px;
    padding: 5px 10px;
    outline: none;

    &:hover {
      background-color: #f7f7f7;
    }

    &:active {
      background-color: #f1f1f1;
    }
  }

  &__slider {
    width: 100%;
    height: 20px;
    margin-bottom: 5px;
    z-index: 0;

    & .handle {
      font-size: 0.9em;
      text-align: center;
      background-color: #fff;
      color: white;
      cursor: pointer;
      border: 1px solid #909eab;
      border-radius: 50%;
      z-index: 0;
    }

    .handle.active {
      background-color: #f1f1f1;
      border-color: #2196F3;
    }

    .bar {
      position: relative;
      background: #ddd;
      cursor: pointer;
    }

    .bar.bar-1 {
      background: #b1b1b1;
    }

    .bar.bar-2 {
      background: #ddd;
    }

    & .bar {
      top: 8px;
      height: 1px;
    }

    & .handle {
      width: 18px;
      height: 18px;
    }

  }
}
